
import React, { Component } from 'react'
import axios from '../axios'

export default class Contact extends Component {

  constructor(props) {
    super(props);
    this.state = {
      name: '',
      email: '',
      message: ''
    }

  }

  handleSubmit = async (e) => {
    e.preventDefault();

    alert('Thank you for your message. I will get to you as soon as possible');
  }
  render() {
    return (

      <section id="contact" data-scroll-index={5}>
        <form className="container" method="post" action="/upload">
          {/*left-------------------------------------*/}
          <div className="contact-left">
            <h1 className="c-l-heading">
              <font style={{ borderBottom: '3px solid #1ED98B' }}>Contact</font>
            </h1>
            {/*name------*/}
            <div className="f-name">
              <font>Name</font>
              <input type="text" id="name" name="name" placeholder="Full Name" />
            </div>
            {/*email------*/}
            <div className="f-email">
              <font>Email</font>
              <input type="email" id="email" name="email" placeholder="Example@gmail.com" />
            </div>
          </div>
          {/*right-----------------------------------------*/}
          <div className="contact-right">
            {/*message------*/}
            <div className="message">
              <font>Message</font>
              <textarea name="message" id="message" name="message" rows={5} cols={20} placeholder="Write Message..." defaultValue={""} />
            </div>
            {/*submit-btn----------*/}
            <button>submit</button>
          </div>
        </form>
      </section>
    )
  }
}
