import React, { Component } from 'react'


export default class Project extends Component {
  render() {
    return (

      <section id="project" className="container" data-scroll-index={2}>
        {/*heading------------*/}
        <div className="s-heading">
          <h1>Project</h1>
        </div>
        {/*services-box-container-------*/}
        <div className="b-container">
          {/*----------box-1--------------*/}
          <a href="https://play.google.com/store/apps/details?id=com.coffee.coffeeapp" target="_blank">
            <div className="s-box">
              {/*type---------*/}
              <div class="s-b-img">
                <div className="s-type">Coffast</div>
                {/*name-----*/}
                <img src="images/pj-app.jpg" alt="Mobile App" />
              </div>

              <div class="s-b-text">
                <a>The Coffast app is a convenient mobile platform for customers to order and purchase coffee with delivery service </a>
                <br></br>
                <p>#Java #XML #Firebase #SQLite</p>
              </div>
            </div>
          </a>

          {/*text---------------*/}

          {/*----------box-2--------------*/}
          <a href="#">
            <div className="s-box">
              {/*type---------*/}
              <div class="s-b-img">
                <div className="s-type">Personal Website</div>
                <img src="images/w1.jpeg" alt="web scraping" />
                {/*name-----*/}

              </div>

              <div class="s-b-text">
                <a>This mobile responsive website connects myself to the world</a>
                <br></br>
                <p>#HTML #Css #JavaScript <br></br>#React Native #MongoDB</p>

              </div>
            </div>
          </a>

          {/*text---------------*/}

          {/*----------box-3--------------*/}
          <a href="./document/agency.xlsx" target="_blank " >
            <div className="s-box">
              {/*type---------*/}
              <div class="s-b-img">
                <div className="s-type">Web Scraping</div>
                {/*name-----*/}
                <img src="images/webscrap.jpg" alt="web scraping" />
              </div>

              <div class="s-b-text">
                <a>The program is built to extract the agency data
                  from a government website (Employment Agencies Portal) and integrate to an Excel file</a>
                <br></br>
                <p>#Node.js #jQeuery</p>
              </div>
            </div>
          </a>
          {/*text---------------*/}

          {/*----------box-4--------------*/}
          <a>
            <div className="s-box">
              {/*type---------*/}
              <div class="s-b-img">
                <div className="s-type">打卡</div>
                {/*name-----*/}
                <img src="images/dacard.png" alt="打卡" />
              </div>

              <div class="s-b-text">
                <a>A local guide app that is built to promote Hong Kong famous attractions and enable people to access them conveniently</a>
                <br></br>
                <p>#Java #XML #Firebase</p>
              </div>
            </div>
          </a>
          {/*text---------------*/}
          {/*----------box-5--------------*/}
          <a>
            <div className="s-box">
              {/*type---------*/}
              <div class="s-b-img">
                <div className="s-type">Trading Bots</div>
                {/*name-----*/}
                <img src="images/metatrader.jpg" alt="Trading EA" />
              </div>

              <div class="s-b-text">
                <a>Most of the expert advisors are built with different technical indicators to realize automated Forex trading</a>
                <br></br>
                <p>#C++</p>
              </div>
            </div>
          </a>
          {/*text---------------*/}

          


        </div>
      </section >
    )
  }
}

